/* --- Animacin de Ataque por pulso --- */
@keyframes pulse-attack {
  0% {
    transform: scale(1);
    opacity: 1;
    stroke-width: 3;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
    stroke-width: 4;
  }
  100% {
    transform: scale(1);
    opacity: 1;
    stroke-width: 3;
  }
}
.animate-pulse-attack {
  animation: pulse-attack 0.8s ease-in-out infinite alternate;
}

/* --- Animacin de nmeros flotantes (dao, curacin, etc.) --- */
@keyframes number-indicator-pop {
  0% {
    transform: translateY(0) scale(0.8);
    opacity: 1;
  }
  50% {
    transform: translateY(-10px) scale(1.3);
    opacity: 1;
  }
  100% {
    transform: translateY(-20px) scale(1.3);
    opacity: 0;
  }
}
.animate-number-indicator-pop,
.animate-damage-pop,
.animate-healing-pop,
.animate-debuff-pop {
  animation: number-indicator-pop 1.8s ease-out forwards;
}

/* --- Animacin de pulso para Mordisco Frentico --- */
@keyframes bitten-pulse {
  0% {
    filter: drop-shadow(0 0 5px rgba(219, 39, 119, 0.7));
  }
  50% {
    filter: drop-shadow(0 0 15px rgba(219, 39, 119, 1));
  }
  100% {
    filter: drop-shadow(0 0 5px rgba(219, 39, 119, 0.7));
  }
}
.animate-bitten-pulse {
  animation: bitten-pulse 1.5s ease-in-out infinite alternate;
}

/* --- Animacin de pulso para Sueo Forzado --- */
@keyframes forced-sleep-pulse {
  0% {
    filter: drop-shadow(0 0 5px rgba(83, 234, 253, 0.7));
  }
  50% {
    filter: drop-shadow(0 0 15px rgba(83, 234, 253, 1));
  }
  100% {
    filter: drop-shadow(0 0 5px rgba(83, 234, 253, 0.7));
  }
}
.animate-forced-sleep-pulse {
  animation: forced-sleep-pulse 1.5s ease-in-out infinite alternate;
}

/* --- Animacin de pulso para Taunt --- */
@keyframes taunt-pulse {
  0% {
    stroke-width: 2;
    transform: scale(1);
  }
  50% {
    stroke-width: 4;
    transform: scale(1.02);
  }
  100% {
    stroke-width: 2;
    transform: scale(1);
  }
}
.animate-taunt-pulse {
  animation: taunt-pulse 1s ease-in-out infinite alternate;
}

/* --- Animacin de pulso combinado (Taunt y Mordida) --- */
@keyframes bitten-taunt-pulse {
  0% {
    stroke-width: 2;
    transform: scale(1);
    stroke: rgba(250, 204, 21, 1);
  }
  50% {
    stroke-width: 4;
    transform: scale(1.05);
    stroke: rgba(219, 39, 119, 1);
  }
  100% {
    stroke-width: 2;
    transform: scale(1);
    stroke: rgba(250, 204, 21, 1);
  }
}
.animate-bitten-taunt-pulse {
  animation: bitten-taunt-pulse 2s ease-in-out infinite alternate;
}

/* --- Animacin de brillo para Emboscada --- */
@keyframes ambush-glow {
  0% {
    filter: drop-shadow(0 0 5px rgba(67, 45, 215, 0.7));
  }
  50% {
    filter: drop-shadow(0 0 15px rgba(67, 45, 215, 1));
  }
  100% {
    filter: drop-shadow(0 0 5px rgba(67, 45, 215, 0.7));
  }
}
.animate-ambush-glow {
  animation: ambush-glow 1.5s ease-in-out infinite alternate;
}

