@keyframes slideInLeft {
  0% { transform: translateX(-100%) scale(0.9); opacity: 0; }
  60% { transform: translateX(10%) scale(1.05); opacity: 1; }
  100% { transform: translateX(0) scale(1); opacity: 1; }
}
.animate-player-left {
  animation: slideInLeft 1.2s ease-out forwards;
}

@keyframes slideInRight {
  0% { transform: translateX(100%) scale(0.9); opacity: 0; }
  60% { transform: translateX(-10%) scale(1.05); opacity: 1; }
  100% { transform: translateX(0) scale(1); opacity: 1; }
}
.animate-player-right {
  animation: slideInRight 1.2s ease-out forwards;
}

@keyframes vsBurst {
  0% { transform: scale(0.5) rotate(-20deg); opacity: 0; text-shadow: 0 0 0 rgba(255,0,0,0); }
  50% { transform: scale(1.4) rotate(0deg); opacity: 1; text-shadow: 0 0 50px rgba(255,0,0,1); }
  100% { transform: scale(1) rotate(0deg); text-shadow: 0 0 30px rgba(255,0,0,0.8); }
}
.animate-vs-burst {
  animation: vsBurst 1.2s ease-out forwards;
}

@keyframes vsPulse {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.1); opacity: 0.9; }
}
.animate-vs-pulse {
  animation: vsPulse 2s ease-in-out infinite;
}

@keyframes lightningLeft {
  0%, 100% { opacity: 0; transform: translateX(-20px) scaleX(0.8); filter: blur(1px); }
  50% { opacity: 1; transform: translateX(0) scaleX(1.1); filter: blur(0); }
}
.animate-lightning-left {
  animation: lightningLeft 0.6s infinite ease-in-out;
}

@keyframes lightningRight {
  0%, 100% { opacity: 0; transform: translateX(20px) scaleX(0.8); filter: blur(1px); }
  50% { opacity: 1; transform: translateX(0) scaleX(1.1); filter: blur(0); }
}
.animate-lightning-right {
  animation: lightningRight 0.6s infinite ease-in-out;
}


@keyframes fadeOutEpic {
  0% {
    opacity: 1;
    transform: scale(1) rotate(0deg);
    filter: brightness(1) blur(0);
  }
  40% {
    opacity: 0.8;
    transform: scale(1.05) rotate(-3deg);
    filter: brightness(1.3) blur(1px);
  }
  70% {
    opacity: 0.5;
    transform: scale(1.2) rotate(5deg) translateY(-10px);
    filter: brightness(2) blur(3px);
  }
  100% {
    opacity: 0;
    transform: scale(1.5) rotate(10deg) translateY(-50px);
    filter: brightness(3) blur(6px);
  }
}

.fade-out-epic {
  animation: fadeOutEpic 1.5s ease-in-out forwards;
    filter: brightness(1) contrast(1.2);
}