.death-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  overflow: hidden;
  animation: irisClose 2s ease forwards 2.5s;
  /* empieza despus de la animacin principal */
}

.death-box {
  text-align: center;
  font-family: "Cinzel", serif;
  color: white;
  position: relative;
}

.killer {
  font-size: 3.5rem;
  color: #ffffff;
  text-shadow: 0 0 20px #87cefa, 0 0 40px #1e90ff;
  /* brillo azul */
  animation: killerAppear 1s ease forwards;
}

.vs {
  font-size: 2rem;
  margin: 0.5rem 0;
  color: #cccccc;
  opacity: 0.8;
  animation: fadeIn 1s ease 0.5s forwards;
}

.victim {
  font-size: 3rem;
  color: #87cefa;
  text-shadow: 0 0 20px #1e90ff, 0 0 40px #00bfff;
  animation: soulFade 2.5s ease forwards 1s;
}

@keyframes irisClose {
  0% {
    clip-path: circle(150% at center);
    /* crculo grande, se ve todo */
  }

  100% {
    clip-path: circle(0% at center);
    /* se cierra hasta el centro */
  }
}

@keyframes killerAppear {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes soulFade {
  0% {
    opacity: 1;
    transform: scale(1);
    text-shadow: 0 0 30px #00bfff;
  }

  50% {
    opacity: 0.7;
    transform: scale(1.2);
    text-shadow: 0 0 60px #87cefa;
  }

  100% {
    opacity: 0;
    transform: scale(0.8) translateY(-50px);
    text-shadow: 0 0 80px #1e90ff;
  }
}

/* Destello inicial */
.death-overlay::before {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(circle, rgba(0, 191, 255, 0.6), transparent 70%);
  opacity: 0;
  animation: soulFlash 1s ease-out forwards;
}

@keyframes soulFlash {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
